.new__hero {
  padding-top: 5px;
  align-items: center;
  padding-left: 34px;
  padding-right: 34px;
  display: flex;
}

.new__hero-container {
  width: 100%;
  min-height: 190px;
  padding-left: 20px;
  padding-right: 20px;
  background-image: url("NewHeroBackgraund.8bd98eb6.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 15px;
  display: flex;
}

.new__hero-container--text {
  color: #fff;
  font-family: Work Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;
}

.new__hero-container--title {
  color: #fff;
  max-width: 250px;
  font-family: Gilroy;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.color__yellow {
  color: #fcd54c;
}

@media screen and (min-width: 768px) {
  .new__hero {
    padding-left: 40px;
    padding-right: 40px;
  }

  .new__hero-container {
    min-height: 300px;
    padding-left: 60px;
    padding-right: 60px;
  }

  .new__hero-container--text {
    font-size: 16px;
  }

  .new__hero-container--title {
    max-width: 420px;
    font-size: 40px;
  }
}

@media screen and (min-width: 1920px) {
  .new__hero {
    padding-left: 60px;
    padding-right: 60px;
  }

  .new__hero-container {
    min-height: 768px;
    padding-bottom: 100px;
    padding-left: 345px;
  }

  .new__hero-container--text {
    font-size: 20px;
  }

  .new__hero-container--title {
    max-width: 660px;
    margin-top: 20px;
    font-size: 60px;
  }
}



